<template>
    <div>
        <div>
            <div style="display: flex; justify-content: space-between">
                <div>
                    <el-input
                            :disabled="showAdvanceSearchView"
                            style="width: 350px; margin-right: 10px;"
                            v-model="keyword"
                            clearable
                            @keydown.enter.native="initEmps"
                            @clear="initEmps"
                            placeholder="通过员工名进行搜索..."
                            type="text" size="small"
                            prefix-icon="el-icon-search">
                    </el-input>
                    <el-button type="primary" icon="el-icon-search" size="small" @click="initEmps" :disabled="showAdvanceSearchView">搜索</el-button>
                    <el-button type="primary" size="small" @click="showAdvanceSearchView = !showAdvanceSearchView">
                        <i class="fa fa-angle-double-down" aria-hidden="true"></i>
                        高级搜索</el-button>
                </div>
                <div>
                    <el-upload
                            style="display: inline-flex; margin-right: 10px;"
                            class="upload-demo"
                            :on-success="onSuccess"
                            :on-error="onError"
                            :before-upload="beforeUpload"
                            :disabled="uploadDisabled"
                            :show-file-list=false
                            action="/employee/basic/import">
                        <el-button type="success" size="small" :icon="uploadIcon" :disabled="uploadDisabled">
                            {{uploadText}}</el-button>
                    </el-upload>
                    <el-button type="success" size="small" @click="exportData" icon="el-icon-download">
                        导出数据</el-button>
                    <el-button type="primary" icon="el-icon-plus" size="small" @click="showAddEmpView">添加员工</el-button>
                </div>
            </div>
            <transition name="slide-fade">
                <div class="seniorSearchDiv" v-show="showAdvanceSearchView">
                    <el-row>
                        <el-col :span="5">
                            政治面貌:
                            <el-select style="width: 130px;" size="mini" v-model="searchValue.politicId" placeholder="请选择政治面貌">
                                <el-option
                                        v-for="item in politicsstatus"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                </el-option>
                            </el-select>
                        </el-col>
                        <el-col :span="4" >
                            民族:
                            <el-select style="width: 130px;" size="mini" v-model="searchValue.nationId" placeholder="请选择民族">
                                <el-option
                                        v-for="item in nations"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                </el-option>
                            </el-select>
                        </el-col>
                        <el-col :span="4">
                            职位:
                            <el-select style="width: 130px;" size="mini" v-model="searchValue.posId" placeholder="请选择职位">
                                <el-option
                                        v-for="item in positions"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                </el-option>
                            </el-select>
                        </el-col>
                        <el-col :span="4">
                            职称:
                            <el-select style="width: 130px;" size="mini" v-model="searchValue.jobLevelId" placeholder="请选择职位">
                                <el-option
                                        v-for="item in joblevels"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                </el-option>
                            </el-select>
                        </el-col>
                        <el-col :span="7">
                            聘用形式:
                            <el-radio-group v-model="searchValue.engageForm">
                                <el-radio label="劳动合同">劳动合同</el-radio>
                                <el-radio label="劳务合同">劳务合同</el-radio>
                            </el-radio-group>
                        </el-col>
                    </el-row>
                    <el-row style="margin-top: 10px;">
                        <el-col :span="5" style="display: inline-flex; align-items: center">
                            所属部门:
                            <el-popover
                                    placement="right"
                                    title="请选择部门"
                                    width="200"
                                    trigger="manual"
                                    v-model="deptVisible">
                                <el-tree :data="depts" default-expand-all :props="defaultProps" @node-click="searchViewHandleNodeClick"></el-tree>
                                <div slot="reference" class="seniorDeptDiv" @click="showDeptView">{{inputDeptName}}</div>
                            </el-popover>
                        </el-col>
                        <el-col :span="10">
                            入职日期:
                            <el-date-picker
                                    style="margin-left: 5px;"
                                    size="mini"
                                    v-model="searchValue.beginDateScope"
                                    value-format="yyyy-MM-dd"
                                    type="daterange"
                                    range-separator="至"
                                    start-placeholder="开始日期"
                                    end-placeholder="结束日期">
                            </el-date-picker>
                        </el-col>
                        <el-col span="5" offset="4">
                            <el-button size="small">取消</el-button>
                            <el-button size="small" type="primary" @click="initEmps('advanced')">搜索</el-button>
                        </el-col>
                    </el-row>
                </div>
            </transition>
        </div>
        <div>
            <el-table
                    v-loading="loading"
                    element-loading-text="数据加载中"
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"
                    :data="emps"
                    size="small "
                    stripe
                    style="width: 100%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        fixed
                        align="left"
                        label="姓名"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="gender"
                        align="left"
                        label="性别"
                        width="60">
                </el-table-column>
                <el-table-column
                        prop="workID"
                        align="left"
                        label="工号"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="birthday"
                        align="left"
                        width="120"
                        label="出生日期">
                </el-table-column>
                <el-table-column
                    prop="idCard"
                    align="left"
                    width="170"
                    label="身份证号码">
                </el-table-column>
                <el-table-column
                    prop="wedlock"
                    align="center"
                    width="80"
                    label="婚姻状况">
                </el-table-column>
                <el-table-column
                    prop="nation.name"
                    align="center"
                    width="70"
                    label="民族">
                </el-table-column>
                <el-table-column
                    prop="nativePlace"
                    align="center"
                    width="80"
                    label="籍贯">
                </el-table-column>
                <el-table-column
                    prop="politicsstatus.name"
                    align="center"
                    width="100"
                    label="政治面貌">
                </el-table-column>
                <el-table-column
                    prop="email"
                    align="left"
                    width="170"
                    label="电子邮件">
                </el-table-column>
                <el-table-column
                    prop="phone"
                    align="left"
                    width="120"
                    label="电话号码">
                </el-table-column>
                <el-table-column
                    prop="address"
                    align="left"
                    width="150"
                    label="联系地址">
                </el-table-column>
                <el-table-column
                    prop="department.name"
                    align="center"
                    width="100"
                    label="所属部门">
                </el-table-column>
                <el-table-column
                    prop="position.name"
                    align="left"
                    width="110"
                    label="职位">
                </el-table-column>
                <el-table-column
                    prop="jobLevel.name"
                    align="left"
                    width="120"
                    label="职称">
                </el-table-column>
                <el-table-column
                    prop="engageForm"
                    align="left"
                    width="120"
                    label="聘用形式">
                </el-table-column>
                <el-table-column
                        prop="school"
                        align="center"
                        width="90"
                        label="毕业院校">
                </el-table-column>
                <el-table-column
                        prop="specialty"
                        align="center"
                        width="100"
                        label="专业">
                </el-table-column>
                <el-table-column
                        prop="tiptopDegree"
                        align="center"
                        width="80"
                        label="最高学历">
                </el-table-column>
                <el-table-column
                    prop="beginDate"
                    align="left"
                    width="120"
                    label="入职日期">
                </el-table-column>
                <el-table-column
                    prop="conversionTime"
                    align="left"
                    width="120"
                    label="转正日期">
                </el-table-column>
                <el-table-column
                        prop="beginContract"
                        align="left"
                        width="120"
                        label="合同起始日期">
                </el-table-column>
                <el-table-column
                        prop="endContract"
                        align="left"
                        width="120"
                        label="合同截止日期">
                </el-table-column>
                <el-table-column
                        align="center"
                        width="100"
                        label="合同期限">
                    <template slot-scope="scope">
                        <el-tag size="small" style="margin-right: 5px;">{{scope.row.contractTerm}}</el-tag>年
                    </template>
                </el-table-column>
                <el-table-column
                        width="200"
                        fixed="right"
                        label="操作">
                    <template slot-scope="scope">
                        <el-button @click="showEditEmpView(scope.row)" style="padding: 3px;" size="mini">编辑</el-button>
                        <el-button style="padding: 3px;" size="mini" type="primary">查看高级资料</el-button>
                        <el-button @click="deleteEmp(scope.row)" style="padding: 3px;" size="mini" type="danger">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div style="margin-top: 10px; display: flex; justify-content: flex-end">
                <el-pagination
                        background
                        layout="sizes, prev, pager, next, jumper, ->, total, slot"
                        @size-change="sizeChange"
                        @current-change="currentChange"
                        :total="total">
                </el-pagination>
            </div>
        </div>
        <div>
            <el-dialog
                    :title="title"
                    :visible.sync="dialogVisible"
                    width="80%">
                <div>
                    <el-form :model="emp" :rules="rules" ref="empForm">
                        <el-row>
                            <el-col :span="6">
                                <el-form-item label="姓名:" prop="name">
                                    <el-input
                                            size="mini"
                                            type="text"
                                            prefix-icon="el-icon-edit"
                                            style="width: 150px;"
                                            placeholder="请输入员工姓名"
                                            v-model="emp.name">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="5">
                                <el-form-item label="性别:" prop="gender">
                                    <el-radio-group v-model="emp.gender">
                                        <el-radio label="男">男</el-radio>
                                        <el-radio label="女">女</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="出生日期:" prop="birthday">
                                    <el-date-picker
                                            style="width: 170px;"
                                            v-model="emp.birthday"
                                            type="date"
                                            size="mini"
                                            format="yyyy-MM-dd"
                                            placeholder="出生日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="7">
                                <el-form-item label="政治面貌:" prop="politicId">
                                    <el-select style="width: 180px;" size="mini" v-model="emp.politicId" placeholder="请选择政治面貌">
                                        <el-option
                                                v-for="item in politicsstatus"
                                                :key="item.id"
                                                :label="item.name"
                                                :value="item.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row>
                            <el-col :span="6">
                                <el-form-item label="民族:" prop="nationId">
                                    <el-select style="width: 150px;" size="mini" v-model="emp.nationId" placeholder="请选择民族">
                                        <el-option
                                                v-for="item in nations"
                                                :key="item.id"
                                                :label="item.name"
                                                :value="item.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="5">
                                <el-form-item label="籍贯:" prop="nativePlace">
                                    <el-input
                                            size="mini"
                                            type="text"
                                            prefix-icon="el-icon-edit"
                                            style="width: 150px;"
                                            placeholder="员工籍贯"
                                            v-model="emp.nativePlace">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="电子邮件:" prop="email">
                                    <el-input
                                            size="mini"
                                            type="text"
                                            prefix-icon="el-icon-message"
                                            style="width: 170px;"
                                            placeholder="电子邮箱地址..."
                                            v-model="emp.email">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="7">
                                <el-form-item label="联系地址:" prop="address">
                                    <el-input
                                            size="mini"
                                            type="text"
                                            prefix-icon="el-icon-edit"
                                            style="width: 180px;"
                                            placeholder="联系地址..."
                                            v-model="emp.address">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row>
                            <el-col :span="6">
                                <el-form-item label="职位:" prop="posId">
                                    <el-select style="width: 150px;" size="mini" v-model="emp.posId" placeholder="请选择职位">
                                        <el-option
                                                v-for="item in positions"
                                                :key="item.id"
                                                :label="item.name"
                                                :value="item.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="5">
                                <el-form-item label="职称:" prop="jobLevelId">
                                    <el-select style="width: 150px;" size="mini" v-model="emp.jobLevelId" placeholder="请选择职位">
                                        <el-option
                                                v-for="item in joblevels"
                                                :key="item.id"
                                                :label="item.name"
                                                :value="item.id">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="所属部门:" prop="departmentId">
                                        <el-popover
                                                placement="right"
                                                title="请选择部门"
                                                width="200"
                                                trigger="manual"
                                                v-model="deptVisible">
                                            <el-tree :data="depts" default-expand-all :props="defaultProps" @node-click="handleNodeClick"></el-tree>
                                            <div slot="reference" class="deptDiv" @click="showDeptView">{{inputDeptName}}</div>
                                        </el-popover>
                                </el-form-item>
                            </el-col>
                            <el-col :span="7">
                                <el-form-item label="电话号码:" prop="phone">
                                    <el-input
                                            size="mini"
                                            prefix-icon="el-icon-phone"
                                            style="width: 180px;"
                                            placeholder="电话号码..."
                                            v-model="emp.phone">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row>
                            <el-col :span="6">
                                <el-form-item label="工号:" prop="workID">
                                    <el-input
                                            size="mini"
                                            style="width: 150px;"
                                            disabled
                                            v-model="emp.workID">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="5">
                                <el-form-item label="学历:" prop="tiptopDegree">
                                    <el-select style="width: 150px;" size="mini" v-model="emp.tiptopDegree" placeholder="最高学历">
                                        <el-option
                                                v-for="item in tiptopDegrees"
                                                :key="item"
                                                :label="item"
                                                :value="item">
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="毕业院校:" prop="school">
                                    <el-input
                                            size="mini"
                                            style="width: 170px;"
                                            prefix-icon="el-icon-edit"
                                            placeholder="毕业院校名称..."
                                            v-model="emp.school">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="7">
                                <el-form-item label="专业名称:" prop="specialty">
                                    <el-input
                                            size="mini"
                                            style="width: 180px;"
                                            prefix-icon="el-icon-edit"
                                            placeholder="专业名称"
                                            v-model="emp.specialty">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row>
                            <el-col :span="6">
                                <el-form-item label="入职日期:" prop="beginDate">
                                    <el-date-picker
                                            style="width: 170px;"
                                            v-model="emp.beginDate"
                                            type="date"
                                            size="mini"
                                            format="yyyy-MM-dd"
                                            placeholder="入职日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="转正日期:" prop="conversionTime">
                                    <el-date-picker
                                            style="width: 170px;"
                                            v-model="emp.conversionTime"
                                            type="date"
                                            size="mini"
                                            format="yyyy-MM-dd"
                                            placeholder="转正日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="合同起始日期:" prop="beginContract">
                                    <el-date-picker
                                            style="width: 170px;"
                                            v-model="emp.beginContract"
                                            type="date"
                                            size="mini"
                                            format="yyyy-MM-dd"
                                            placeholder="合同起始日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="合同终止日期:" prop="endContract">
                                    <el-date-picker
                                            style="width: 170px;"
                                            v-model="emp.endContract"
                                            type="date"
                                            size="mini"
                                            format="yyyy-MM-dd"
                                            placeholder="合同终止日期">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row>
                            <el-col :span="8">
                                <el-form-item label="身份证号码:" prop="idCard">
                                    <el-input
                                            size="mini"
                                            style="width: 200px;"
                                            prefix-icon="el-icon-edit"
                                            placeholder="身份证号码"
                                            v-model="emp.idCard">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="8">
                                <el-form-item label="聘用形式:" prop="engageForm">
                                    <el-radio-group v-model="emp.engageForm">
                                        <el-radio label="劳动合同">劳动合同</el-radio>
                                        <el-radio label="劳务合同">劳务合同</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </el-col>
                            <el-col :span="8">
                                <el-form-item label="婚姻状况:" prop="wedlock">
                                    <el-radio-group v-model="emp.wedlock">
                                        <el-radio label="已婚">已婚</el-radio>
                                        <el-radio label="未婚">未婚</el-radio>
                                        <el-radio label="离异">离异</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="doUpdateAndAddEmp">确 定</el-button>
                    <el-button  @click="resetForm">重 置</el-button>
                </span>
            </el-dialog>
        </div>

    </div>
</template>

<script>
    export default {
        name: "EmpBasic",
        data() {
            var checkphone = (rule, value, callback) => {
                if (value == "") {
                    callback(new Error("请输入手机号"));
                } else if (!this.isCellPhone(value)) {
                    callback(new Error("手机号码格式不正确"));
                } else {
                    callback();
                }
            };
            var checkIdCard = (rule, value, callback) => {
                if (value == "") {
                    callback(new Error("请输入身份证号"));
                } else if (!this.isCellIdCard(value)) {
                    callback(new Error("身份证号格式不正确"));
                } else {
                    callback();
                }
            };
            return {
                emps: [],
                searchValue: {
                    politicId: null,
                    nationId: null,
                    posId: null,
                    jobLevelId: null,
                    engageForm: '劳务合同',
                    departmentId: null,
                    beginDateScope: null
                },
                total: 0,
                page: 1,
                size: 10,
                title: '',
                deptVisible: false,
                showAdvanceSearchView: false,
                loading: false,
                keyword: '',
                dialogVisible: false,
                uploadText: '导入数据',
                uploadIcon: 'el-icon-upload2',
                uploadDisabled: false,
                nations: [],
                politicsstatus: [],
                joblevels: [],
                positions: [],
                tiptopDegrees: [
                    '本科',
                    '硕士',
                    '博士',
                    '大专',
                    '高中',
                    '初中',
                    '小学',
                    '其他'
                ],
                emp: {
                    name: "",
                    gender: "",
                    birthday: "",
                    idCard: "",
                    wedlock: "",
                    nationId: null,
                    nativePlace: "",
                    politicId: null,
                    email: "",
                    phone: "",
                    address:"",
                    departmentId: null,
                    jobLevelId: null,
                    posId: null,
                    engageForm: "",
                    tiptopDegree: "",
                    specialty: "",
                    school: "",
                    beginDate: "",
                    workState: "在职",
                    workID: "",
                    contractTerm: 2.0,
                    conversionTime: "",
                    notWorkDate: null,
                    beginContract: "",
                    endContract: "",
                    workAge: null
                },
                depts: [],
                inputDeptName: '所属部门',
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                rules: {
                    name: [{ required: true, message: '请输入员工姓名', trigger: 'blur'}],
                    gender: [{ required: true, message: '请选择性别', trigger: 'blur'}],
                    birthday: [{ required: true, message: '请输入员工出生日期', trigger: 'blur'}],
                    idCard: [{ required: true, validator: checkIdCard, trigger: 'blur'}],
                    wedlock: [{ required: true, message: '请选择婚姻状况', trigger: 'blur'}],
                    nationId: [{ required: true, message: '请选择民族', trigger: 'blur'}],
                    nativePlace: [{ required: true, message: '请输入籍贯', trigger: 'blur'}],
                    politicId: [{ required: true, message: '请选择政治面貌', trigger: 'blur'}],
                    email: [{ required: true, message: '请输入邮箱', trigger: 'blur'}, {
                        type: 'email',
                        message: '邮箱格式不正确',
                        trigger: 'blur'
                    }],
                    phone: [{ required: true, validator: checkphone, trigger: 'blur'}],
                    address: [{ required: true, message: '请输入员工地址', trigger: 'blur'}],
                    departmentId: [{ required: true, message: '请选择员工所属部门', trigger: 'blur'}],
                    jobLevelId: [{ required: true, message: '请选择职称', trigger: 'blur'}],
                    posId: [{ required: true, message: '请选择职位', trigger: 'blur'}],
                    engageForm: [{ required: true, message: '请选择聘用形式', trigger: 'blur'}],
                    tiptopDegree: [{ required: true, message: '请选择学历', trigger: 'blur'}],
                    specialty: [{ required: true, message: '请输入专业名称', trigger: 'blur'}],
                    school: [{ required: true, message: '请输入毕业院校', trigger: 'blur'}],
                    beginDate: [{ required: true, message: '请选择入职日期', trigger: 'blur'}],
                    workState: [{ required: true, message: '请输入在职状态', trigger: 'blur'}],
                    workID: [{ required: true, message: '请输入员工工号', trigger: 'blur'}],
                    contractTerm: [{ required: true, message: '请输入合同期限', trigger: 'blur'}],
                    conversionTime: [{ required: true, message: '请选择转正日期', trigger: 'blur'}],
                    beginContract: [{ required: true, message: '请选择合同起始日期', trigger: 'blur'}],
                    endContract: [{ required: true, message: '请选择合同终止日期', trigger: 'blur'}],
                }
            }
        },
        mounted() {
          this.initEmps();
          this.initData();
          this.initPositions();
        },
        methods: {
            searchViewHandleNodeClick(data) {
                this.inputDeptName = data.name;
                this.searchValue.departmentId = data.id;
                this.deptVisible = !this.deptVisible;
            },
            exportData() {
                window.open("/employee/basic/export", "_parent");
            },
            // 上传文件成功的钩子
            onSuccess(response, file, fileList) {
                this.uploadText = "导入数据";
                this.uploadIcon = "el-icon-upload2"
                this.uploadDisabled = false;
                this.initEmps();
            },
            // 文件上传失败的钩子
            onError(err, file, fileList) {
                this.uploadText = "导入数据";
                this.uploadIcon = "el-icon-upload2"
                this.uploadDisabled = false;
            },
            // 上传文件之前的钩子
            beforeUpload() {
                this.uploadText = "正在导入";
                this.uploadIcon = "el-icon-loading"
                this.uploadDisabled = true;
            },
            // 重置emp对象属性
            resetEmp() {
                this.emp = {
                    name: "", gender: "", birthday: "",
                    idCard: "", wedlock: "", nationId: null,
                    nativePlace: "", politicId: null, email: "",
                    phone: "", address:"", departmentId: null,
                    jobLevelId: null, posId: null, engageForm: "",
                    tiptopDegree: "", specialty: "", school: "",
                    beginDate: "", workState: "在职", workID: "",
                    contractTerm: 2.0, conversionTime: "", notWorkDate: null,
                    beginContract: "", endContract: "", workAge: null
                };
            },
            deleteEmp(data) {
                this.$confirm('此操作将永久删除【'+ data.name +'】该员工, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/employee/basic/" + data.id).then(resp => {
                       if (resp) {
                           this.initEmps();
                       }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            resetForm() {
                this.$refs['empForm'].resetFields();
                this.getMaxWorkId();
            },
            doUpdateAndAddEmp() {
                /**
                 *  如果id存在则发送更新请求
                 *  否则发送添加请求
                 */
                if (this.emp.id) {
                    this.$refs['empForm'].validate((valid) => {
                        if (valid) {
                            this.putRequest("/employee/basic/", this.emp).then(resp => {
                                if (resp) {
                                    this.dialogVisible = false;
                                    this.initEmps();
                                }
                            })
                        }
                    });
                } else {
                    this.$refs['empForm'].validate((valid) => {
                        if (valid) {
                            this.postRequest("/employee/basic/", this.emp).then(resp => {
                                if (resp) {
                                    this.dialogVisible = false;
                                    this.initEmps();
                                    this.resetEmp();
                                }
                            })
                        }
                    });
                }
            },
            showEditEmpView(data) {
                this.title = "修改员工";
                this.emp = data;
                this.inputDeptName = data.department.name;
                this.initPositions();
                this.dialogVisible = true;
            },
            // 自定义手机号码校验规则
            isCellPhone(val) {
                if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
                    return false;
                } else {
                    return true;
                }
            },
            // 自定义校验身份证号校验规则
            isCellIdCard(val) {
                if (!/(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/.test(val)) {
                    return false;
                } else {
                    return true;
                }
            },
            handleNodeClick(data) {
                this.inputDeptName = data.name;
                this.emp.departmentId = data.id;
                this.deptVisible = !this.deptVisible;
            },
            showDeptView() {
                this.deptVisible = !this.deptVisible;
            },
            getMaxWorkId() {
                this.getRequest("/employee/basic/maxWorkId").then(resp => {
                    if (resp) {
                        this.emp.workID = resp.object;
                    }
                });
            },
            initPositions() {
                this.getRequest("/employee/basic/positions").then(resp => {
                   if (resp) {
                       this.positions = resp;
                   }
                });
            },
            initData() {
                if (!window.sessionStorage.getItem("nations")) {
                    this.getRequest("/employee/basic/nations").then(resp => {
                        if (resp) {
                            this.nations = resp;
                            window.sessionStorage.setItem("nations", JSON.stringify(this.nations));
                        }
                    });
                } else {
                    this.nations = JSON.parse(window.sessionStorage.getItem("nations"));
                }

                if (!window.sessionStorage.getItem("joblevels")) {
                    this.getRequest("/employee/basic/joblevels").then(resp => {
                        if (resp) {
                            this.joblevels = resp;
                            window.sessionStorage.setItem("joblevels", JSON.stringify(this.joblevels));
                        }
                    });
                } else {
                    this.joblevels = JSON.parse(window.sessionStorage.getItem("joblevels"));
                }

                if (!window.sessionStorage.getItem("politicsstatus")) {
                    this.getRequest("/employee/basic/politicsstatus").then(resp => {
                        if (resp) {
                            this.politicsstatus = resp;
                            window.sessionStorage.setItem("politicsstatus", JSON.stringify(this.politicsstatus));
                        }
                    });
                } else {
                    this.politicsstatus = JSON.parse(window.sessionStorage.getItem("politicsstatus"));
                }

                if (!window.sessionStorage.getItem("depts")) {
                    this.getRequest("/employee/basic/depts").then(resp => {
                        if (resp) {
                            this.depts = resp;
                            window.sessionStorage.setItem("depts", JSON.stringify(this.depts));
                        }
                    });
                } else {
                    this.depts = JSON.parse(window.sessionStorage.getItem("depts"));
                }
            },
            initEmps(type) {
                this.loading = true;
                let url = '/employee/basic/?page=' + this.page + "&size=" + this.size;
                if (type && type == 'advanced') {
                    if (this.searchValue.politicId != null) {
                        url += "&politicId=" + this.searchValue.politicId;
                    }

                    if (this.searchValue.nationId != null) {
                        url += "&nationId=" + this.searchValue.nationId;
                    }

                    if (this.searchValue.posId != null) {
                        url += "&posId=" + this.searchValue.posId;
                    }

                    if (this.searchValue.jobLevelId != null) {
                        url += "&jobLevelId=" + this.searchValue.jobLevelId;
                    }

                    if (this.searchValue.engageForm != null || this.this.searchValue.engageForm != '') {
                        url += "&engageForm=" + this.searchValue.engageForm;
                    }

                    if (this.searchValue.departmentId != null) {
                        url += "&departmentId=" + this.searchValue.departmentId;
                    }

                    if (this.searchValue.beginDateScope != null) {
                        url += "&beginDateScope=" + this.searchValue.beginDateScope;
                    }
                } else {
                    url += '&name=' + this.keyword;
                }
                this.getRequest(url).then(resp => {
                    if (resp) {
                        this.emps = resp.data;
                        this.total = resp.total;
                        this.loading = false;
                    }
                });
            },
            showAddEmpView() {
                this.title = "添加员工"
                this.initPositions();
                this.getMaxWorkId();
                this.dialogVisible = true;
            },
            currentChange(currentPage) {
                this.page = currentPage;
                this.initEmps();
            },
            sizeChange(pageSize) {
                this.size = pageSize;
                this.initEmps();
            },
            seniorSearch() {
                let url = '/employee/basic/?page=' + this.page + "&size=" + this.size;
                if (this.searchValue.politicId != null) {
                    url += "&politicId=" + this.searchValue.politicId;
                }

                if (this.searchValue.nationId != null) {
                    url += "&nationId=" + this.searchValue.nationId;
                }

                if (this.searchValue.posId != null) {
                    url += "&posId=" + this.searchValue.posId;
                }

                if (this.searchValue.jobLevelId != null) {
                    url += "&jobLevelId=" + this.searchValue.jobLevelId;
                }

                if (this.searchValue.engageForm != null || this.this.searchValue.engageForm != '') {
                    url += "&engageForm=" + this.searchValue.engageForm;
                }

                if (this.searchValue.departmentId != null) {
                    url += "&departmentId=" + this.searchValue.departmentId;
                }

                if (this.searchValue.beginDateScope != null) {
                    url += "&beginDateScope=" + this.searchValue.beginDateScope;
                }
            }
        }
    }
</script>

<style>
    .slide-fade-enter-active {
        transition: all .8s ease;
    }
    .slide-fade-leave-active {
        transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to
        /* .slide-fade-leave-active for below version 2.1.8 */ {
        transform: translateX(10px);
        opacity: 0;
    }
    .deptDiv {
        width: 170px;
        height: 26px;
        font-size: 13px;
        border: 1px solid #ddeedd;
        display: inline-flex;
        border-radius: 5px;
        cursor: pointer;
        margin-top: 5px;
        align-items: center;
        padding-left: 10px;
        box-sizing: border-box;
    }

    .seniorSearchDiv {
        border: 1px solid #409eff;
        border-radius: 5px;
        box-sizing: border-box;
        padding: 5px;
        margin: 10px 0px;
    }

    .seniorDeptDiv {
        width: 130px;
        font-size: 13px;
        display: inline-flex;
        border: 1px solid #dedede;
        height: 25px;
        border-radius: 5px;
        cursor: pointer;
        align-items: center;
        padding-left: 8px;
        margin-left: 5px;
        margin-top: 5px;
        box-sizing: border-box;
    }
</style>